<template>
	<view>
		<!-- 顶部选项卡 -->
		<scroll-view scroll-x :scroll-into-view="scrollInto" class="scroll-row scroll-with-animation">
			<view @click="changeTab(index)" v-for="(item,index) in tabBars" :key="index" :class="tabIndex === index ? 'text-main font-lg font-wight-blod':''"
			 :id="'tab'+index" class="scroll-row-item px-3 py-2" style="height: 100rpx;">
				{{item.name}}
			</view>
		</scroll-view>
		<swiper :current="tabIndex" :duration="150" @change="onChangeTab" :style="'height:'+scrollH+'px;'">
			<swiper-item v-for="(item,index) in tabBars" :key="index">
				<scroll-view scroll-y="true" :style="'height:'+scrollH+'px;'">
					<view v-for="i in 10">
						{{i}}
					</view>
				</scroll-view>
			</swiper-item>
		</swiper>
		<!--
		<block v-for="(item,index) in list" :key="index">
			<!-- 首页列表组件 -->
		<!--
			<common-list @follow="follow" @doSupport ="doSupport" :item="item" :index="index"></common-list>
			<!-- 全局分割线 -->
		<!--
			<divider></divider>
		</block>
		-->
	</view>
</template>

<script>
	import commonList from '@/components/common/common-list.vue'
	export default {
		data() {
			return {
				//列表高度
				scrollH: 600,
				tabIndex: 0,
				scrollInto: "",
				tabBars: [{
						name: '关注'
					},
					{
						name: '体育'
					},
					{
						name: '关注'
					},
					{
						name: '体育'
					},
					{
						name: '关注'
					},
					{
						name: '体育'
					},
					{
						name: '关注'
					},
					{
						name: '体育'
					},
				],
				list: [{
					username: "昵称",
					userpic: "/static/default.jpg",
					newstime: "2020-2-2",
					isFollow: false,
					title: "我是标题",
					titlepic: "/static/demo/datapic/11.jpg",
					support: {
						type: "support",
						support_count: 1,
						unsupport_count: 2
					},
					comment_count: 2,
					share_num: 2
				}, {
					username: "昵称",
					userpic: "/static/default.jpg",
					newstime: "2020-2-2",
					isFollow: false,
					title: "我是标题",
					titlepic: "/static/demo/datapic/11.jpg",
					support: {
						type: "unsupport",
						support_count: 1,
						unsupport_count: 2
					},
					comment_count: 2,
					share_num: 2
				}, {
					username: "昵称",
					userpic: "/static/default.jpg",
					newstime: "2020-2-2",
					isFollow: false,
					title: "我是标题",
					titlepic: "/static/demo/datapic/11.jpg",
					support: {
						type: "",
						support_count: 0,
						unsupport_count: 0
					},
					comment_count: 0,
					share_num: 0
				}]
			}
		},
		components: {
			commonList
		},
		onLoad() {
			//计算屏幕高度
			uni.getSystemInfo({
				success: res => {
					this.scrollH = res.windowHeight - uni.upx2px(100)
				}
			})
		},
		methods: {
			//监听滑动
			onChangeTab(e) {
				this.changeTab(e.detail.current)
			},
			//点击切换tab选项
			changeTab(index) {
				if (this.tabIndex === index) {
					return
				}
				this.tabIndex = index
				//滚动到指定元素
				this.scrollInto = 'tab' + index

			},
			//关注（子组件传过来的方法）
			follow(index) {
				this.list[index].isFollow = true
				uni.showToast({
					title: "关注成功"
				})
			},
			//顶踩 （子组件方法）
			doSupport(type) {
				//拿到当前对象
				let item = this.list[type.index]
				let msg = type.type === 'support' ? '赞' : '踩'
				//判断之前是否已经顶踩
				//之前没有操作过
				if (item.support.type === '') {
					item.support[type.type + '_count']++
				}
				//之前已经点赞过了
				if (item.support.type === 'support' && type.type === 'unsupport') {
					//点赞-1
					item.support.support_count--;
					//踩+1
					item.support.unsupport_count++;
				} else if (item.support.type === 'unsupport' && type.type === 'support') {
					//点赞+1
					item.support.support_count++;
					//踩-1
					item.support.unsupport_count--;
				}
				item.support.type = type.type;
				uni.showToast({
					title: msg + '成功'
				})
			}
		}
	}
</script>
